.about{
    padding: 60px 40px;
    /* height: 465px; */
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: center;
    width: 1100px;
    margin: auto;

    .image{
        align-self: flex-end;
        width: fit-content;
        /* background-color: #0048c4; */
        position: relative;
        img{
            width: 380px; 
            /* margin-bottom: -48px; */
            filter: brightness(.98);
        } 
        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 60px; /* Set the height of the gradient */
            background: linear-gradient(0deg, rgb(8, 18, 35) 10%, rgba(255, 255, 255, 0) 80%);
            z-index: 5; /* Ensures the overlay is on top of the image */
            pointer-events: none; /* Allows clicking through the overlay */
        }
    }

    .content{
        white-space: unset !important;

        h1{
            color: #bfc5cc;
            font-size: 50px;
            margin-bottom: 15px;

            span{
                font-family: "Anonymous Pro";
                letter-spacing: -5px;
                font-size: 50px;
    
                &:nth-child(2){
                    letter-spacing: -5px;
                }
            }
        }

        p{
            width: 530px;
            color: #bfc5cc;
            letter-spacing: 2px;
        }

        button{
            font-family: 'NeueMachina';
            letter-spacing: 1px;
            background-color: transparent;
            /* color: #2c7aff; */
            box-shadow: inset 0px 0px 15px #0064c8;
            color: #d2e1ff;
            font-weight: 500;
            border-radius: 30px;
            border: none;
            outline: none;
            /* margin: 30px 0; */
            margin-top: 30px;
            padding: 10px 15px;
            font-size: 17px;
            display: flex;
            align-items: center;
            gap: 8px;
            cursor: pointer;
            transition: .5s;

            &:hover{
                box-shadow: inset 0px 0px 35px #0064c8;
            }

            i{
                font-size: 20px;
                font-weight: 100;
                height: 20px;
                width: 20px;
            }
        }
    }
}

@media screen and (max-width: 834px) {
    .about{
        width: unset;
        padding: 0 20px 40px 20px;
        height: max-content;
        flex-direction: column;

        .image{
            align-self: unset;
            img{
                width: 500px;
                margin-bottom: 0;
            }
        }

        .content{
            padding: 30px;

            h1{
                margin-bottom: 20px;
            }

            p {
                width: unset;
                font-size: 22px;
                margin-bottom: 15px;
            }

            button{
                font-size: 20px;
            }
        }
    }
}

@media screen and (max-width: 480px) {
    .about{
        padding: 50px 10px 30px 10px;

        .image{
            img{
                width: 400px;
            }
        }

        .content{
            padding: 10px;

            h1{
                font-size: 34px;
            }

            p{
                font-size: 16px;
                line-height: 1.4;
            }
        }
    }
}