.contact{
    padding: 40px 0 70px 0;
    width: 1100px;
    margin: 0 auto;
    
    h1{
        font-size: 44px;
        color: #bfc5cc;
        margin-left: 40px;
        margin-bottom: 30px;

        span{
            font-family: "Anonymous Pro";
            letter-spacing: -5px;
            font-size: 50px;

            &:nth-child(2){
                letter-spacing: -5px;
            }
        }
    }

    .contact-form-div{
        display: flex;
        /* align-items: center; */
        gap: 80px;

    .contact-form {
        width: 60%;
        display: grid;
        gap: 15px;
        margin-left: 30px;

        input, 
        textarea,
        button.submit {
            border: none;
            outline: none;
            resize: none;
            padding: 15px;
            border-radius: 5px;
            background-color: #00225d;
            color: #fff;
        }

        .form-span {
            display: flex;
            align-items: center;
            width: 100%;
            gap: 10px;
            margin: 0;

            .form-email,
            .form-mobile {
                width: 50%;

            }
        }

        button.submit {
            background-color: #005bc9;
            width: fit-content;
            font-size: 16px;
            padding: 15px 20px;
            display: flex;
            align-items: center;
            gap: 8px;
            transition: .3s;
            cursor: pointer;
            font-family: 'NeueMachina';
            letter-spacing: 1px;
            animation: shadowAnimS 8s linear infinite;

            &:hover {
                background-color: #004598;
            }
        }

    }

    .message{
        background-color: #005bc9;
        width: fit-content;
        padding: 6px 10px 6px 6px;
        color: #fff;
        position: fixed;
        top: 10px;
        left: 0;
        right: 0;
        margin: auto;
        border-radius: 5px;
        display: flex;
        align-items: center;
        gap: 6px;
        
        i{
            font-size: 30px;
        }
    }

    .social-media{
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        width: 350px;
        align-content: center;
        height: fit-content;

        color: #bfc5cc;

        a{
            color: #bfc5cc;
            text-decoration: none;
        }

        .mini-card{
            /* width: 104px; */
            border-radius: 8px;
            /* display: grid;
            justify-items: center;
            align-items: center; */
            background-color: #00225d;
            padding: 16px;
            text-align: center;
            letter-spacing: 1px;
            font-size: 13px;
            transition: .4s;
            animation: shadowAnimS 8s linear infinite;

            &.mail{
                width: fit-content;
                /* padding: 12px; */
            }

            &:hover{
                background-color: #003a81;
            }

            i{
                font-size: 30px;
            }
        }
    }
}
}

@media screen and (max-width: 834px) {
    .contact{
        padding: 20px 20px 40px 20px;
        width: unset;

        .contact-form-div{
            flex-direction: column;
            gap: 35px;

            .contact-form{
                width: 95%;
                margin: 20px auto;
            }

            .social-media{
                width: 98%;
                padding: 20px;
                justify-content: center;

                .mail{
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}

@media screen and (max-width: 480px) {
    .contact{
        padding: 20px 20px 50px 20px;

        h1{
            font-size: 34px;
            margin-left: 0;
        }

        .contact-form-div{

            .contact-form{
                margin-top: 10px;
                margin-left: 0;
                width: 100%;
            }

            .social-media{
                width: 100%;
                margin-left: 0;
                justify-content: center;
                padding: 0;

                .mini-card{
                    width: 62px;
                    /* min-width: 150px; */

                    &.mail{
                        width: 366px;
                        justify-content: center;
                    }
                }
            }
        }
    }
}