.navbar {
    display: flex;
    align-items: center;
    position: fixed;
    top: 15px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: fit-content;
    padding: 5px;
    justify-content: space-around;
    z-index: 10;
    border-radius: 36px;
    background: rgba( 0, 102, 244, 0.25 );
    backdrop-filter: blur( 2px );


    .nav-buttons {
        color: white;
        display: flex;
        justify-content: center;
        gap: 25px;

        button {
            background: none;
            border: none;
            color: #ffff;
            font-size: 1rem;
            cursor: pointer;
            padding: 8px 16px;
            font-family: "NeueMachina";
            border-radius: 36px;
            transition: 0.4s;
            outline: #00234d;

            &:hover {
                background: #005dde8f;
            }
        }
    }

    .nav-toggle{
        display: none;
        padding: 2px;
        border-radius: 0 0 6px 6px;
        overflow: hidden;

        button{
            background-color: #002f7f;
            border: none;
            outline: none;
            color: #fff;
            font-size: 55px;
            transform: scaleX(-1);
        }
    }
}

@media screen and (max-width: 834px) {
    .navbar{
        position: fixed;
        left: unset;
        /* right: 20px; */
        top: unset;
        flex-direction: column;
        align-items: start;
        background-color: rgba(0, 45, 109, 0.609);
        padding: 0;
        border-radius: 0;

        &.expanded {
            animation: widthAnimation 1s forwards linear;
            padding: 20px 0 20px 20px;
        }

        .logo{
            display: none;
            margin-top: -40px;
            margin-bottom: 20px;
        }

        .nav-buttons{
            display: none;
            flex-direction: column;
            gap: 15px;
            width: -webkit-fill-available;
            margin-left: -20px;

            button{
                /* color: #00234d; */
                border: none;
                font-size: 1.5rem;
                text-align: left;
                padding: 10px 20px;
                border-radius: 0;
            }
        }

        .nav-toggle{
            background-color: #002f7f;
            display: flex;
            align-items: center;
            align-self: flex-end;
            position: fixed;
            right: 20px;
            top: 0;
        }

        .navbar{
            .nav-toggle{
                button{
                    font-size: 50px;
                }
            } }
    }
}

@keyframes widthAnimation {
    0%{
        width: 0;
    }
    50%{
        width: 50%;
    }
    100%{
        width: 95.4%;
    }
}

@media screen and (max-width: 480px) {
    .navbar{
    .nav-toggle{
        button{
            font-size: 36px;
        }
    } }
}