.footer{
    padding: 40px 20px;
    font-family: 'Space Mono';
    letter-spacing: 2px;
    color: #bfc5cc;
    width: 1000px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    button{
        font-family: 'JetBrains Mono';
        border: none;
        outline: none;
        background-color: #00225d;
        padding: 8px;
        transform: rotate(-90deg);
        font-size: 24px;
        font-weight: 700;
        border-radius: 50px;
        color: #bfc5cc;
        position: absolute;
        top: -50px;
        right: 50px;
        transition: .4s;
        &:hover{
            background-color: #002f82;
        }
    }

    p{
        font-size: 16px;
        display: flex;
        align-items: center;
        gap: 5px;

        i{
            font-size: 18px;
            color: #0055ff;
        }

        .copyright{ 
            color: #bfc5cc;
            font-size: 20px;
        }
    }
}


@media screen and (max-width: 834px) {
    .footer{
        width: -webkit-fill-available;
    }

}

@media screen and (max-width: 480px) {
    .footer{
        padding: 50px 20px 30px 20px;
        flex-direction: column;
        gap: 10px;

        button{
            top: -25px;
            right: 35px;
        }
    }

}