.home {
    color: #fff;
    display: flex;
    align-items: center;
    height: calc(100dvh - 32px);
    gap: 60px;
    flex-direction: column;
    width: 988px;
    margin: auto auto;
    justify-content: center;
    position: relative;

    .top-gradient{
        /* background-color: #dd003f; */
        width: 1000px;
        height: 300px;
        position: absolute;
        top: -32px;
        background-repeat: no-repeat;
        background-position: top center;
        background-size: 100%;
        background-image: url(../../../public/topGradient.svg);
        filter: brightness(1.2);
    }

    .name {
        z-index: 5;

        .name-container {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            width: 720px;

            .letter {
                text-align: center;
                font-size: 70px;
                letter-spacing: 5px;
                background-color: #00112e; 
                background-color: #081223; 
                border: 2px solid #ffffff31;
                border-width: 2px 0px 2px 2px;
                height: 115px;
                width: 80px;
                line-height: 100%;
                box-sizing: border-box;
                flex: 0 0 auto;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                color: #ffffff54;
                font-family: "Space Mono";

                p {
                    position: relative;
                    width: 100%;
                    text-align: center;
                    left: 3px;
                }

                &.active{
                    color: #bfc5cc;
                }

                &:first-child{
                    font-family: "Anonymous Pro";
                    color: #ffd700;
                    /* font-weight: 600; */
                    font-size: 80px;
                }
                &:last-child{
                    border-right-width: 2px;
                    font-family: "Anonymous Pro";
                    color: #dd003f; 
                    font-size: 80px;
                }

                &:nth-child(9){
                    border-right-width: 2px;
                }
                &:nth-child(10){
                    border-width: 2px 0 0 0;
                    color: transparent;
                }
                &:nth-child(-n+7) {
                    border-bottom-width: 0px;
                }
            }
        }

        .role {
            height: 28px;
            margin: 20px 82px;

            h4 {
                font-size: 20px;
                font-weight: 400;
                letter-spacing: 2px;
                display: inline-block;
                white-space: nowrap;
                overflow: hidden;
                border-right: 2px solid rgb(255, 255, 255) !important; /* Cursor */
                animation: blink-caret 0.75s step-end infinite;
            }
        }
    }

    .arrow-icon{
        transform: rotate(90deg);
        margin-bottom: -100px;

        span{
            font-family: 'JetBrains Mono';
            font-size: 40px;
            color: #2c7aff;
        }
    }

    .codelines {
        /* background-color: aquamarine; */
        display: flex;
        height: max-content;
        flex-wrap: wrap;
        gap: 16px;
        justify-content: end;
        width: 520px;
        /* align-self: flex-start; */
        /* transform: translateY(-50%); */

        .line {
            height: 10px;
            border-radius: 16px;
            position: relative;
            overflow: hidden;
            background-color: #ffffff55;
            transform: scaleX(-1) !important;
            transition: 0.4s;

            /* &:hover{
                width: 150px !important;
            } */

            .bg {
                position: absolute;
                top: 0;
                height: 100%;
                width: 5%;
                /* width: 100%; */
                animation: increaseWidth 3s forwards;
                animation-delay: 1.5s;
            }
        }
    }
}

@keyframes blink-caret {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: black;
    }
}

@keyframes increaseWidth {
    0% {
        width: 5%;
    }

    100% {
        width: 100%;
    }
}

@media screen and (max-width: 834px) {
    .home {
        width: 100vw;
        padding: 0;
        flex-direction: column;
        align-items: center;
        height: 100vh;

        .top-gradient{
            height: 50%;
            background-size: 165%;
        }

        .name {
            h3 {
                font-size: 40px;
            }
            h1 {
                font-size: 150px;
            }
            .role {
                h4 {
                    font-size: 30px;
                }
            }
        }

        .codelines {
            width: 695px;
            /* width: 765px; */
            align-self: flex-end;
        }
    }
}

@media screen and (max-width: 480px) {
    .home {
        gap: 0px;
        padding: 10px;
        height: 100vh;
        justify-content: space-evenly;

        .top-gradient {
            background-size: 80%;
            height: unset;
        }

        .name {
            padding: 0 10px;
            zoom: .57;

            h3 {
                font-size: 26px;
            }
            h1 {
                font-size: 86px;
                margin-top: 20px;
                margin-left: -5px;
            }
            .role {
                h4 {
                    margin-top: 50px;
                }
            }
        }

        .codelines {
            gap: 14px;
            width: 360px;
            /* if thre is not +9 350px width */
            margin-bottom: -80px;
        }
    }
}
