@import url(https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@24,400,1,0);
.skills{
    padding: 40px 0;
    /* width: 550px; */
    margin: 0 auto;

    h1{
        font-size: 44px;
        color: #bfc5cc;
        /* margin-left: 40px; */

        span{
            font-family: "Anonymous Pro";
            letter-spacing: -5px;
            font-size: 50px;

            &:nth-child(2){
                letter-spacing: -5px;
            }
        }
    }

    .skillset{
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 30px 0;

        .skill{ 
            display: flex;
            align-items: center;
            color: #bfc5cc;
            gap: 10px;
            background-color: #00225d;
            padding: 10px;
            width: 420px;
            border-radius: 5px;
            letter-spacing: 1px;
            position: relative;
            overflow: hidden;
            animation: shadowAnimS 6s linear infinite;
            animation-delay: 2s;

            span{
                position: absolute;
                height: 42px;
                margin-left: -10px;
                background-color: #00225d;
                background-color: #031c47;

            }

            i{
                z-index: 2;
                font-size: 22px;
                font-weight: 300;
                color: #dcdfe3;
            }
            p{
                z-index: 2;
            }
            .right{
                z-index: 2;
                font-size: 14px;
                position: absolute;
                right: 10px;
            }
        }
    }
}

.service{
    padding: 40px 0;
    /* width: 550px; */
    margin: 0 auto;

    h1{
        font-size: 44px;
        color: #bfc5cc;
        /* margin-left: 40px; */
        margin-bottom: 30px;

        span{
            font-family: "Anonymous Pro";
            letter-spacing: -5px;
            font-size: 50px;

            &:nth-child(2){
                letter-spacing: -5px;
            }
        }
    }

    .serve-cards{
        display: flex;
        flex-direction: column;
        gap: 32px;

        .serve-card{
            padding: 18px 20px;
            color: #bfc5cc;
            display: flex;
            flex-direction: column;
            gap: 8px;
            /* border: 1px #0046c0 solid; */
            border-radius: 6px;
            width: 404px;
            background-image: url(../../../public/topGradient.svg);
            background-repeat: no-repeat;
            background-position: top center;
            background-size: 250%;
            position: relative;
            overflow: hidden;
            animation: shadowAnimS 8s linear infinite;
            transition: .4s;

            h3{
                font-weight: 500;
            }

            p{
                font-size: 13px;
                font-weight: 300;
                letter-spacing: 1px;
                color: #bfc5cc;

            }
        }
    }
}

@keyframes shadowAnimS {
    0% {
        box-shadow: 0px 1px 0px #0256e8; /* bottom */

    }
    20% {
        box-shadow: -1px 0px 0px #0256e8; /* left */

    }
    40% {
        box-shadow: 0px -1px 0px #0256e8; /* top */

    }
    60% {
        box-shadow: 0px 0px 0px #0256e8; /* no */

    }
    66%,
    100% {
        box-shadow: 0px 0px 0px #0256e8; /* Pause effect */
    }
}

@media screen and (max-width : 834px) {
    .skills{
        width: 100vw !important;
        padding: 20px;
        align-items: flex-start;

        h1{
            /* margin-left: 30px; */
            margin-bottom: 20px;
        }
        .skillset{
            display: grid;
            grid-template-columns: repeat(2, auto);
            padding: 10px 0px;

            .skill{
                width: -webkit-fill-available;

                .right{
                    font-weight: 500;
                }
            }
        }
    }

    .service{
        padding: 40px 50px;

        h1{
            margin-left: 0;
            margin-bottom: 30px;
        }

        .serve-cards{
            display: flex;
            align-items: center;
            gap: 40px;

            .serve-card{
                /* height: 204px; */
                width: -webkit-fill-available;

                p{
                    font-size: 16px;
                }
            }
        }
    }
}

@media screen and (max-width: 480px) {
    .skills{
        padding: 20px 20px;

        h1{
            font-size: 34px;
            margin-left: 0;
        }

        .skillset{
            grid-template-columns: repeat(1, auto);
            padding: 10px 0; 
            gap: 15px;

            .skill{
                width: -webkit-fill-available;

                /* span{
                    display: none;
                } */

                .right{
                    p{
                        font-size: 12px !important;
                    }
                }
            }
        }
    }

    .service{
        padding: 20px;

        h1{
            font-size: 34px;
        }

        .serve-cards{
            flex-direction: column;
            gap: 10px;

            .serve-card{
                padding: 15px;
                height: fit-content;
                /* width: 95%; */
                margin-bottom: 0;

                h3{
                    font-weight: 600;
                    letter-spacing: 2px;
                }

                p{
                    font-size: 16px;
                    line-height: 1.4;
                }
            }
        }
    }
}


