@font-face {
    font-family: 'NeueMachina';
    src: url("../public/NeueMachina-Regular.otf") format("opentype");
}

.container {
    margin: auto;
    scroll-behavior: smooth;
    overflow: auto; 
    display: grid;
    outline: none;

} 

.card {
    vertical-align: top;
    display: inline-block;
    border-radius: 8px;
    font-family: 'NeueMachina';

    &.card1{
        height: 100dvh; 
    }

    .bar{
        display: flex;
        padding: 8px 15px;
        align-items: center;
        gap: 5px;
        background-color: #00225c;
        
        .red{
            background-color: gold;
            width: 16px;
            height: 16px;
            border-radius: 50%;
        }
        .blue{
            background-color: #004cab;
            width: 16px;
            height: 16px;
            border-radius: 50%;
        }
        .yellow{
            background-color: #dd003f;  
            width: 16px;
            height: 16px;
            border-radius: 50%;
        }
    }
}


@media screen and (max-width: 834px) {

    .container{
        width: 100%;
        padding-left: 0;
        padding-top: 0;
        display: contents;
        margin-top: 0;
    }

    .card{
        width: 100%;
        height: max-content !important;
        margin-right: 0;
        border: none;


        .bar{
            .red, .blue, .yellow{
                width: 20px;
                height: 20px;
            }
        }

    }
    
}
