.projects {
    padding: 40px;
    width: 1100px;
    margin: 0 auto;

    h1 {
        font-size: 44px;
        color: #bfc5cc;
        margin-left: 20px;
        margin-bottom: 20px;

        span{
            font-family: "Anonymous Pro";
            letter-spacing: -5px;
            font-size: 50px;

            &:nth-child(2){
                letter-spacing: -5px;
            }
        }
    }

    .proj-cards {
        padding: 20px;
        display: grid;
        align-items: stretch;
        justify-content: center;
        gap: 18px;
        grid-template-columns: repeat(4, auto);

        .project {
            width: 220px;
            background-color: #031c47;
            padding: 15px;
            border-radius: 8px;
            overflow: hidden;
            transition: 0.3s;
            /* box-shadow: 2px 2px 2px whitesmoke; */

            &:hover{
                animation: shadowAnim 5s linear infinite;
            }

            .head-link {
                display: flex;
                align-items: center;
                justify-content: space-between;

                i {
                    color: #bfc5cc;
                    font-size: 28px;
                }
            }

            img {
                width: 250px;
                margin: -15px;
                margin-bottom: 12px;
            }

            h3 {
                color: #bfc5cc;
                letter-spacing: 2px;
                margin-bottom: 12px;
            }

            .tech-info {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 10px;

                .techs {
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    p {
                        background-color: #002f7faf;
                        padding: 2px 6px;
                        text-align: center;
                        letter-spacing: 1px;
                        border-radius: 36px;
                    }
                }
                i {
                    color: #bfc5cc;
                    font-size: 20px;
                    padding: 2px;
                    background-color: #0043b4;
                    border-radius: 5px;
                }
            }

            p {
                color: #bfc5cc;
                font-size: 13px;
                letter-spacing: 2px;
            }
        }
    }
}

@keyframes shadowAnim {
    0% {
        box-shadow: 1px 1px 0px #0044ba;
    }
    25% {
        box-shadow: -1px 1px 0px  #0044ba;
    }
    50% {
        box-shadow: 0px 0px 0px  #0044ba;
    }
    75% {
        box-shadow: 0px 0px 0px  #0044ba;
    }
    90% {
        box-shadow: 0px 0px 0px  #0044ba;
    }
    100%{
        box-shadow: 0px 0px 0px  #0044ba;
    }
}

@media screen and (max-width: 834px) {
    .projects {
        padding: 40px;
        width: unset;

        h1 {
            margin-left: 10px;
            margin-bottom: 10px;
        }

        .proj-cards {
            display: grid;
            grid-template-columns: repeat(2, auto);

            .project {
                width: 366px;

                img {
                    width: -webkit-fill-available;
                }

                p {
                    font-size: 16px;
                }
            }
        }
    }
}

@media screen and (max-width: 480px) {
    .projects {
        padding: 20px;

        h1 {
            font-size: 34px;
            margin-left: 0;
        }

        .proj-cards {
            overflow-x: scroll;
            padding: 15px 0;
            gap: 15px;
            grid-template-columns: repeat(1, auto) !important;

            .project {
                /* padding: 10px; */
                /* height: 260px; */
                /* width: 100%; */

                img {
                    transform: unset;
                }

                h3 {
                    font-size: 22px;
                }

                p {
                    line-height: 1.4;
                }
            }
        }
    }
}
